import React, { useEffect ,useState} from 'react'
import NextForcastList from './NextForcastList';
import { useWeather } from '../context/WeatherContext'; 

function NextForcastWeather() {
  const [forecastData,setForecastData]=useState(null)
  const { todayData,setTodayData } = useWeather();

  
  useEffect(()=>{
    getData()
  },[todayData])

  let getData=async ()=>{
    if(todayData==null){
    let response = await fetch('http://127.0.0.1:8000/api/forecast')

      

    let data=await response.json()
    let arr=[]
    for(let i=0;i<data.list.length;i+=8){
        // arr.push(Object.keys(data.list[i.toString()]).map((key) => [key, data.list[i.toString()][key]]));
        arr.push(Object.keys(data.list[i.toString()]).map((key) => [key, data.list[i.toString()][key]]));

    }


    setForecastData(arr)
  }
  else{
   
    const response = await fetch(`http://localhost:8000/api/forecastUpdate`,{
      'method':'PUT',
      'headers':{
          'Content-Type':'application/json',
     
      },
      credentials: 'same-origin',
      'body':JSON.stringify(todayData)
  })

  if (response.ok) {
    const data2 = await response.json();  // Ensure you parse the JSON
  
    let arr=[]
    for(let i=0;i<data2.list.length;i+=8){
        // arr.push(Object.keys(data.list[i.toString()]).map((key) => [key, data.list[i.toString()][key]]));
        arr.push(Object.keys(data2.list[i.toString()]).map((key) => [key, data2.list[i.toString()][key]]));


    setForecastData(arr)
  

  }
  }


}
}

  return (
    <div className="NextForcastWeather">
        <div style={{width:'80%', borderRadius:'10px',border:'1px solid white',overflow:'hidden'}}>

    
          {forecastData?forecastData.map((arr,index)=>{
              return <NextForcastList key={index} data={arr}/>
          }):""}
            
           
        </div>
    </div>
  )
}

export default NextForcastWeather