import React, { useEffect, useState } from 'react'
import BedtimeIcon from '@mui/icons-material/Bedtime';
import ForecastList from './TodayForecastList';
import { useWeather } from '../context/WeatherContext';


function CurrentWeather({data1}) {

  const [currentTemp,setCurrentTemp]=useState(null)
  const [forecastTemp,setForecastTemp]=useState(null)
  const { todayData, setTodayData } = useWeather();
  

  const degreeSymbol = "\u00B0";


  useEffect(()=>{
   
    getCurrentData();
    getForecast();
    }
   
     
  ,[todayData])


  let getCurrentData=async ()=>{
    // let response = await fetch('http://localhost:8000/api/')
    // let data=await response.json()
    // setCurrentTemp(data)
    console.log(todayData,"checking current data")
    if(todayData==null){
    let response = await fetch(`http://localhost:8000/api/`,{
      'method':'PUT',
      'headers':{
          'Content-Type':'application/json',
     
      },
      credentials: 'same-origin',
      'body':JSON.stringify(todayData)
  })
    let data=await response.json()
    setCurrentTemp(data)



  }
  else{
    setCurrentTemp(todayData)
  }
}

  let getForecast=async()=>{
    if(todayData==null){
    let response2 = await fetch('http://localhost:8000/api/forecast')
    let data2=await response2.json()
    let arr=[]
    arr.push(data2.list['0'])
    arr.push(data2.list['1'])
    let index=0

    for(let i=1;i<data2.list.length;i++){
      if(data2.list[i.toString()]['dt_txt'].split(" ")[0] == arr[index.toString()]['dt_txt'].split(" ")[0]){
        arr.push(data2.list[i.toString()])
        index=index+1
      }
    }
    arr = [...new Set(arr)]
    
  setForecastTemp(arr)
        }
          
  else{
    setCurrentTemp(todayData)
    const response = await fetch(`http://localhost:8000/api/forecastUpdate`,{
      'method':'PUT',
      'headers':{
          'Content-Type':'application/json',
      
      },
      credentials:'same-origin',
      'body':JSON.stringify(todayData)
  })

  if(response.ok) {
    const data2 = await response.json();  // Ensure you parse the JSON
    let arr=[]
    arr.push(data2.list['0'])
    arr.push(data2.list['1'])
    let index=0

    for(let i=1;i<data2.list.length;i++){
      if(data2.list[i.toString()]['dt_txt'].split(" ")[0] == arr[index.toString()]['dt_txt'].split(" ")[0]){
        arr.push(data2.list[i.toString()])
        index=index+1
      }
    }
    arr = [...new Set(arr)]
  
  setForecastTemp(arr)
  }
}
  }

    



  return (

    
    <div className="CurrentWeather">
        <div className="currentData">
            <div className="CurrentDetail">
                
                <h1 style={{fontSize:100,fontFamily:"Serif"}}>{currentTemp !== null?currentTemp.main.temp.toString().split('.')[0]:'...'}{degreeSymbol} F</h1>
                <p>Feels Like {currentTemp !== null?currentTemp.main.feels_like.toString().split('.')[0]:'...'}{degreeSymbol} F</p>
            </div>
            <div >
          
                    {currentTemp?<img  src={"http://openweathermap.org/img/wn/" +  currentTemp.weather[0].icon + "@2x.png"} alt="BigCo Inc. logo"/>:""}
                
            </div>
        </div>
        
        
        <div className="currentForecast">
          

           {currentTemp !== null && forecastTemp!==null ?forecastTemp.map((a,index)=>{ return <ForecastList forecastData={a} key={index}/>
          }):"..."}
              
       
            

        </div>

    </div>
  )
}

export default CurrentWeather