import React, { useEffect } from "react";
import * as Collapsible from "@radix-ui/react-collapsible";
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';
import FavoriteIcon from '@mui/icons-material/Favorite';
import { useWeather } from '../context/WeatherContext';
import '../radixcss.css';


const CollapsibleDemo = () => {
	const { favoritesData, setFavoritesData } = useWeather();
	const { todayData, setTodayData } = useWeather();
	const [open, setOpen] = React.useState(false);
	const [favorites,setFavorites]=React.useState([]);

	useEffect(()=>{
		getData()
	  },[favoritesData])
	
	  let getData=async ()=>{

		let response = await fetch('http://localhost:8000/api/favorites')
		let data=await response.json()
		
		
		
		setFavorites(data)
	}

	const favSelect=async (fav)=>{
		console.log(fav,"here")
		let response = await fetch(`http://localhost:8000/api/`,{
			'method':'PUT',
			'headers':{
				'Content-Type':'application/json',
		   
			},
			credentials: 'same-origin',
			'body':JSON.stringify(fav.favoritesdata)
		})
		  let data=await response.json()
		  setTodayData(data)
	}
	

	return (
		<Collapsible.Root
			className="CollapsibleRoot"
			open={open}
			onOpenChange={setOpen}
		>
			<div
				style={{
					display: "flex",
					alignItems: "center",
					justifyContent: "space-between",
				}}
			>
				<span style={{ color: "Black" }}>
					<FavoriteIcon/> My Favorites
				</span>

				<div style={{marginBottom:'80px'}}></div>

				<Collapsible.Trigger asChild>
					<button className="IconButton">
						{open ? <ExpandLessIcon style={{height:'25px',width:'25px'}}/> : <ExpandMoreIcon style={{height:'25px',width:'25px'}}/>}
					</button>
				</Collapsible.Trigger>
			</div>


			<Collapsible.Content>
				
				{favorites.map((favorite,index)=>(
					<div onClick={() => favSelect(favorite)} key={index}  className="Repository" >
						<p className="Text">{favorite.favorites}</p>
						<p className="Text" style={{color:"grey"}}>{favorite.country}</p>
					
					</div>
					))}
			
			</Collapsible.Content>
		</Collapsible.Root>
	);
};

export default CollapsibleDemo;

