import React, { use, useEffect, useState } from 'react'
import ThermostatIcon from '@mui/icons-material/Thermostat';
import LocationTabs from './LocationTabs';
import CollapsibleDemo from './Collapsable';
import worldsData from './worldcities.csv';
import Papa from 'papaparse';
import { useWeather } from '../context/WeatherContext'; // Import the context hook



function Sidebar() {

  const { todayData,setTodayData } = useWeather();
  const { favoritesData,setFavoritesData } = useWeather();
  const [value,setValue]=useState('')

  const [data,setData]=useState([])




  useEffect(()=>{
    fetchCities()
    getData()
  },[favoritesData])




  const fetchCities = async () => {
   
    const response = await fetch(worldsData);
    const text = await response.text();
    Papa.parse(text, {
      header: true, // Set to true if your CSV has a header row
      complete: (results) => {
       
        setData(results.data);
        
      },
    });
    
  };

  let getData=async ()=>{
    if(todayData==null){
      let response = await fetch('http://127.0.0.1:8000/api/')
      let data=await response.json()
      setTodayData(data)
    }
   

}
  const handleChange=(e)=>{
    setValue(e.target.value)
  }

  const onSearch=(searchTerm)=>{
      setValue(searchTerm)



  }

  const addFavorite=async ()=>{
    if(todayData!=null){
    const response = await fetch(`http://localhost:8000/api/favoritesAdd`,{
      'method':'PUT',
      'headers':{
          'Content-Type':'application/json',
     
      },
      credentials: 'same-origin',
      'body':JSON.stringify(todayData)
  })
  setFavoritesData(todayData)
}
}

  const onSubmit= async (submitTerm)=> {
    let foundValue="lll"
    if(value!==null){
      foundValue = data.find(element => element.city=== submitTerm)
      
    };

    const response = await fetch(`http://localhost:8000/api/searchLocation`,{
      'method':'PUT',
      'headers':{
          'Content-Type':'application/json',
     
      },
      credentials: 'same-origin',
      'body':JSON.stringify(foundValue)
  })

  if (response.ok) {
    const data2 = await response.json();  // Ensure you parse the JSON
    setTodayData(data2)

  }


  

}






  

  return (
    <div className='Sidebar'>
        <div className="Logo">
        
            <ThermostatIcon style={{width:'100px',height:'50px',color:'purple'}}/>
             <span style={{fontSize:20}}>WeatherZone</span>

             
            
       
        </div>
        <hr style={{background: 'white',height:'2px',borderColor:'white'}}/>
        <div className="SidebarMain">

  
            
         {todayData?<LocationTabs todayData={todayData}/>:"Loading"}
            

            
        
          <CollapsibleDemo favoritesData={favoritesData}/>
          <div className="Search-container">
            <div className="search-inner" >
              <input type='text' value={value} onChange={handleChange}/>
              <button onClick={()=>onSubmit(value)}> Search </button>
            </div>
            <div className="dropdown">
                {value?
                data.filter(item=>{
            
                  const searchTerm=value.toLowerCase();
                  const fullName=item.city.toLowerCase()
      

                  return searchTerm &&  fullName.startsWith(searchTerm) && fullName!==searchTerm
                }).slice(0,5)
                .map((item,index)=>(
                      <div key={index} onClick={()=>onSearch(item.city)} className='dropdown-row'>{item.city} , {item.country}</div>
                ))
              
              :""}

            </div>


          </div>

          <button className="add_to_favorite" onClick={addFavorite}>Add to Favorites</button>
          
    
   
      </div>
       

    </div>
  )
}

export default Sidebar