import React from 'react'
import WbSunnyIcon from '@mui/icons-material/WbSunny';

function ForecastList({forecastData}) {
  const degreeSymbol = "\u00B0";
  return (
    <>
      <div className='TodayForecastList'>
          <p>{forecastData['dt_txt'].split(" ")[1].split(":")[0]}:00</p>
          {forecastData?<img  src={"http://openweathermap.org/img/wn/" +  forecastData.weather[0].icon + ".png"} alt="BigCo Inc. logo"/>:""}
                
    
          <p>{forecastData['main'].temp.toString().split('.')[0]}{degreeSymbol} F</p>

      </div>
    </>
  )
}

export default ForecastList