import React from 'react'
import WbSunnyIcon from '@mui/icons-material/WbSunny';

function NextForcastList({data}) {
  let weather=data['1']['1']
  const degreeSymbol = "\u00B0";
  let date=""
  if(data.length===9){
     date=data['8']['1'].split(" ")[0]}
  else{
     date=data['9']['1'].split(" ")[0]}
  

  return (


      
    <div className="NextForcastList">
         <p>{date}</p>
         <div className='div2' style={{display:'flex',gap:15 }}>
            
            <p>{weather.temp_max.toString().split('.')[0]}{degreeSymbol} F max /{weather.temp_min.toString().split('.')[0]} {degreeSymbol} F min</p>
            {/* {console.log(data[2][1][0].icon)} */}
            {data?<img  src={"http://openweathermap.org/img/wn/" +  data[2][1][0].icon + ".png"} alt="BigCo Inc. logo"/>:""}
               
  
            
        </div>
         
      </div>
  )
  }


export default NextForcastList